import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { UserTasksComponent } from './user-tasks.component';
import { RouterModule, Routes, ROUTES } from '@angular/router';
import { UsersTasksListComponent } from './users-tasks-list/users-tasks.component';
import { UsersTaskDetailsComponent } from './users-task-details/users-task-details.component';
import { AuthGuard } from '../login/auth.guard';

const routes:Routes=[
      {path:'',component:UserTasksComponent,
      canActivate:[AuthGuard],
      children:[
        {path:'tasksList',component:UsersTasksListComponent},

        {path:':id', component:UsersTaskDetailsComponent}
     
      ]
        
    }
]

@NgModule({
  declarations: [UserTasksComponent ,UsersTasksListComponent,UsersTaskDetailsComponent],
  imports: [
    CommonModule,
    RouterModule.forChild(routes)
  ],
  exports:[RouterModule]
})
export class UserTasksModule { }
